'use client';

import type { FC } from 'react';
import { DollarSign, PiggyBank, TrendingDown, Wallet } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { formatCurrency } from '@/lib/utils';
import { cn } from '@/lib/utils';

interface SummaryCardsProps {
  dailyBudget: number;
  spentToday: number;
  remainingToday: number;
  earnedToday: number;
}

const SummaryCards: FC<SummaryCardsProps> = ({
  dailyBudget,
  spentToday,
  remainingToday,
  earnedToday,
}) => {
  return (
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium">Earned Today</CardTitle>
          <DollarSign className="h-4 w-4 text-muted-foreground" />
        </CardHeader>
        <CardContent>
          <div className="text-2xl font-bold font-mono text-primary">
            {formatCurrency(earnedToday)}
          </div>
          <p className="text-xs text-muted-foreground">
            Total income logged today.
          </p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium">Daily Budget</CardTitle>
          <PiggyBank className="h-4 w-4 text-muted-foreground" />
        </CardHeader>
        <CardContent>
          <div className="text-2xl font-bold font-mono">
            {formatCurrency(dailyBudget < 0 ? 0 : dailyBudget)}
          </div>
          <p className="text-xs text-muted-foreground">
            Your suggested spending limit for today.
          </p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium">Spent Today</CardTitle>
          <TrendingDown className="h-4 w-4 text-muted-foreground" />
        </CardHeader>
        <CardContent>
          <div className="text-2xl font-bold font-mono">
            {formatCurrency(spentToday)}
          </div>
          <p className="text-xs text-muted-foreground">
            Total expenses logged today.
          </p>
        </CardContent>
      </Card>
      <Card
        className={cn(
          remainingToday < 0 ? 'border-destructive/50 bg-destructive/10' : 'border-accent/50 bg-accent/10'
        )}
      >
        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
          <CardTitle className="text-sm font-medium text-accent-foreground">Net for Today</CardTitle>
          <Wallet className={cn('h-4 w-4', remainingToday < 0 ? 'text-destructive' : 'text-accent')} />
        </CardHeader>
        <CardContent>
          <div className={cn('text-2xl font-bold font-mono', remainingToday < 0 ? 'text-destructive' : 'text-accent-foreground')}>
            {formatCurrency(remainingToday)}
          </div>
          <p className={cn("text-xs", remainingToday < 0 ? 'text-destructive/80' : 'text-accent-foreground/80')}>
            {remainingToday >= 0 ? 'Net income for today.' : 'Net loss for today.'}
          </p>
        </CardContent>
      </Card>
    </div>
  );
};

export default SummaryCards;
