'use client';

import type { FC } from 'react';
import { useState, useEffect } from 'react';
import { Calculator, PlusCircle } from 'lucide-react';
import { getSypExchangeRate } from '@/ai/flows/currency-converter';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Skeleton } from '@/components/ui/skeleton';
import { formatCurrency } from '@/lib/utils';
import type { SypExchangeRateOutput } from '@/ai/flows/currency-converter';
import { Label } from '../ui/label';
import type { Expense } from '@/lib/types';

interface SypCalculatorProps {
  onAddExpense: (expense: Omit<Expense, 'id' | 'userId' | 'date'>) => void;
}

const SypCalculator: FC<SypCalculatorProps> = ({ onAddExpense }) => {
  const [rates, setRates] = useState<SypExchangeRateOutput | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [sypAmount, setSypAmount] = useState<string>('');
  const [description, setDescription] = useState('');
  const [usdAmount, setUsdAmount] = useState<number>(0);

  useEffect(() => {
    const fetchRates = async () => {
      setIsLoading(true);
      setError(null);
      try {
        const result = await getSypExchangeRate();
        setRates(result);
      } catch (e) {
        setError('Failed to fetch exchange rates.');
        console.error(e);
      }
      setIsLoading(false);
    };
    fetchRates();
  }, []);

  useEffect(() => {
    if (rates && sypAmount) {
      const syp = parseFloat(sypAmount);
      if (!isNaN(syp) && rates.sell > 0) {
        const converted = syp / rates.sell;
        setUsdAmount(converted);
      } else {
        setUsdAmount(0);
      }
    } else {
      setUsdAmount(0);
    }
  }, [sypAmount, rates]);

  const handleAddExpense = () => {
    if (usdAmount > 0 && rates) {
      onAddExpense({
        description: description || `Expense from ${sypAmount} SYP`,
        amount: usdAmount,
        sypAmount: parseFloat(sypAmount),
        buyRate: rates.buy,
        sellRate: rates.sell,
      });
      setSypAmount('');
      setDescription('');
    }
  };

  const handleRateChange = (field: 'buy' | 'sell', value: string) => {
    if (rates) {
      const newRate = parseFloat(value);
      if (!isNaN(newRate)) {
        setRates({ ...rates, [field]: newRate });
      }
    }
  }

  return (
    <Card className="bg-card/80 backdrop-blur-sm">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Calculator className="text-accent" />
          <span>SYP to USD Calculator</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="flex flex-col gap-4">
        {isLoading ? (
          <div className="space-y-4">
            <Skeleton className="h-6 w-1/2" />
            <Skeleton className="h-10 w-full" />
            <Skeleton className="h-6 w-1/4" />
          </div>
        ) : error ? (
          <p className="text-destructive">{error}</p>
        ) : rates ? (
          <>
            <div className="grid grid-cols-2 gap-4">
               <div className="space-y-2">
                <Label htmlFor="buy-rate">Buy Rate (SYP)</Label>
                <Input
                  id="buy-rate"
                  type="number"
                  value={rates.buy}
                  onChange={(e) => handleRateChange('buy', e.target.value)}
                />
               </div>
               <div className="space-y-2">
                <Label htmlFor="sell-rate">Sell Rate (SYP)</Label>
                <Input
                  id="sell-rate"
                  type="number"
                  value={rates.sell}
                  onChange={(e) => handleRateChange('sell', e.target.value)}
                />
               </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="syp-description">Expense Description</Label>
              <Input
                id="syp-description"
                placeholder="e.g., Lunch with brother"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
              />
            </div>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 items-end">
              <div className="space-y-2">
                <Label htmlFor="syp-amount" className="text-sm font-medium">
                  Amount in SYP
                </Label>
                <Input
                  id="syp-amount"
                  type="number"
                  placeholder="Enter amount in SYP"
                  value={sypAmount}
                  onChange={(e) => setSypAmount(e.target.value)}
                  className="text-lg"
                />
              </div>
              <div className="flex items-center gap-2">
                <div className="space-y-2 flex-grow">
                    <Label className="text-sm font-medium">Amount in USD</Label>
                    <p className="text-2xl font-bold font-mono h-10 flex items-center">
                    {formatCurrency(usdAmount)}
                    </p>
                </div>
                <Button onClick={handleAddExpense} disabled={!usdAmount || usdAmount <= 0} size="icon">
                  <PlusCircle />
                  <span className="sr-only">Add as Expense</span>
                </Button>
              </div>
            </div>
          </>
        ) : null}
      </CardContent>
    </Card>
  );
};

export default SypCalculator;
