'use client';

import type { FC } from 'react';
import { zodResolver } from '@hookform/resolvers/zod';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { PlusCircle, ShoppingBag, Trash2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Separator } from '@/components/ui/separator';
import { formatCurrency } from '@/lib/utils';
import type { Expense } from '@/lib/types';

const expenseFormSchema = z.object({
  description: z.string().min(1, 'Description is required.'),
  amount: z.coerce.number().positive('Amount must be positive.'),
});

type ExpenseFormValues = z.infer<typeof expenseFormSchema>;

interface ExpenseTrackerProps {
  expenses: Expense[];
  onAddExpense: (values: ExpenseFormValues) => void;
  onRemoveExpense: (id: string) => void;
}

const ExpenseTracker: FC<ExpenseTrackerProps> = ({
  expenses,
  onAddExpense,
  onRemoveExpense,
}) => {
  const form = useForm<ExpenseFormValues>({
    resolver: zodResolver(expenseFormSchema),
    defaultValues: {
      description: '',
      amount: '' as any,
    },
  });

  const onSubmit = (data: ExpenseFormValues) => {
    onAddExpense(data);
    form.reset();
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <ShoppingBag />
          <span>Today's Expenses</span>
        </CardTitle>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <div className="flex flex-col sm:flex-row gap-2 items-start">
              <FormField
                control={form.control}
                name="description"
                render={({ field }) => (
                  <FormItem className="flex-grow w-full">
                    <FormLabel className="sr-only">Description</FormLabel>
                    <FormControl>
                      <Input placeholder="e.g., Coffee, Lunch..." {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="amount"
                render={({ field }) => (
                  <FormItem className="w-full sm:w-auto">
                    <FormLabel className="sr-only">Amount</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        step="0.01"
                        placeholder="Amount"
                        className="sm:w-32"
                        {...field}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
            <Button type="submit" className="w-full sm:w-auto">
              <PlusCircle className="mr-2" /> Add Expense
            </Button>
          </form>
        </Form>
        <Separator className="my-6" />
        <h3 className="text-lg font-medium mb-2">Logged Expenses</h3>
        <ScrollArea className="h-48 pr-4">
          {expenses.length === 0 ? (
            <p className="text-muted-foreground text-center py-8">
              No expenses logged for today.
            </p>
          ) : (
            <ul className="space-y-2">
              {expenses.map((expense) => (
                <li
                  key={expense.id}
                  className="flex justify-between items-center bg-secondary/50 p-2 rounded-md"
                >
                  <div className="flex flex-col">
                    <span className="text-sm text-foreground capitalize">
                      {expense.description}
                    </span>
                    <span className="font-mono text-sm font-semibold text-muted-foreground">
                      {formatCurrency(expense.amount)}
                    </span>
                  </div>
                  <Button
                    variant="ghost"
                    size="icon"
                    className="text-muted-foreground hover:text-destructive"
                    onClick={() => onRemoveExpense(expense.id)}
                  >
                    <Trash2 className="w-4 h-4" />
                    <span className="sr-only">Remove expense</span>
                  </Button>
                </li>
              ))}
            </ul>
          )}
        </ScrollArea>
      </CardContent>
    </Card>
  );
};

export default ExpenseTracker;
