'use server';

/**
 * @fileOverview A flow for fetching SYP to USD exchange rates.
 *
 * - getSypExchangeRate - A function that returns the exchange rate.
 * - SypExchangeRateOutput - The return type for the getSypExchangeRate function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'genkit';

const SypExchangeRateOutputSchema = z.object({
  buy: z.number().describe('The buy price for 1 USD in SYP.'),
  sell: z.number().describe('The sell price for 1 USD in SYP.'),
});

export type SypExchangeRateOutput = z.infer<typeof SypExchangeRateOutputSchema>;

export async function getSypExchangeRate(): Promise<SypExchangeRateOutput> {
  return sypExchangeRateFlow();
}

const sypExchangeRateFlow = ai.defineFlow(
  {
    name: 'sypExchangeRateFlow',
    outputSchema: SypExchangeRateOutputSchema,
  },
  async () => {
    // In a real application, you would fetch this from an API.
    // For this demo, we are returning mock data.
    // Based on rates from late 2024.
    return {
      buy: 14500,
      sell: 14700,
    };
  }
);
